#bot-bar {
  position: fixed;
  padding: 5px 0;
  left: 0;
  right: 0;
  bottom: 0;
  height: 40px;
  border-top: solid 1px #e3e3e3;
  background-color: #fff;
  font-size: 0;
  z-index: 100;
}

#bot-bar .button {
  position: relative;
  height: 100%;
  width: 50px;
}

#bot-bar .button .box {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  transition: .3s ease;
}

#bot-bar .button .box::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 2px;
  background-color: #e3000f;
  transform-origin: center;
  transform: scaleX(0);
  transition: .3s ease;
}

#bot-bar .button img {
  position: relative;
  padding: 5px;
  height: 100%;
  z-index: 1;
}

#bot-bar .button:hover .box::before, #bot-bar .button.active .box::before {
  transform: scaleX(1);
}

#search-popup {
  display: flex;
